<template>
  <v-alert  
      border="start"
      variant="tonal"
      color="green"
      class="w-[60%]"
      v-if="level==10">

    ¡Has ganado!

  </v-alert>
  <div class="py-4">
    <h1>Munchkin Level: {{level}}</h1>
    <h1>Munchkin Atack: {{atack}}</h1>
  </div>

  <!-- cuerpo -->
  <div>
    <v-table theme="dark" @change="levelM()" >
      <thead>
        <tr>
          <th>Lugar</th>
          <th>Puntos</th>
          <th>Habilitado</th>
        </tr>
      </thead>
      
      <tbody class="p-6">
        <tr>
          <td>Cabeza</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="cabeza[0]"/></td>
          <td><input type="checkbox" v-model="cabeza[1]"  /></td>
        </tr>

        <tr>
          <td>Cuerpo</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="cuerpo[0]"/></td>
          <td><input type="checkbox" v-model="cuerpo[1]"  /></td>
        </tr>

        <tr>
          <td>Pies</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="pies[0]"/></td>
          <td><input type="checkbox" v-model="pies[1]"  /></td>
        </tr>
        
        

        <tr v-if="!dobleMano[1]">
          <td>Mano 1</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="brazo1[0]"/></td>
          <td><input type="checkbox" v-model="brazo1[1]"  /></td>
        </tr>
        <tr v-if="!dobleMano[1]">
          <td>Mano 2</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="brazo2[0]"/></td>
          <td><input type="checkbox" v-model="brazo2[1]"  /></td>
        </tr>
        <tr v-if="dobleMano[1]">
          <td>Doble mano</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="dobleMano[0]"/></td>
          <td><input type="checkbox" v-model="dobleMano[1]"  /></td>
        </tr>

        <tr>
          <td>Objetos Extras</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="extras[0]"/></td>
          <td><input type="checkbox" v-model="extras[1]"  /></td>
        </tr>

        <tr>
          <td>Multiplicador</td>
          <td><input type="number" class="border rounded-xl text-center" v-model="multi"/></td>
          <td></td>
        </tr>
      </tbody>
    </v-table>
    <p class="py-6">Doble Mano: <input type="checkbox" v-model="dobleMano[1]" @change="levelM()" /></p>

    <div class="flex flex-row justify-around justify-items-center gap-6 py-4">
      <button class="rounded-md border-slate-800 bg-blue-500 py-2 px-5" @click="sum">+</button>
      <button class="rounded-md border-slate-800 bg-yellow-500 py-2 px-5" @click="min">-</button>
    </div>

    <button class="rounded-md border-slate-800 bg-red-500 py-2 px-5" @click="reset">Reset</button>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      cabeza:[0,true],
      cuerpo:[0,true],
      pies:  [0,true],
      brazo1:[0,true],
      brazo2:[0,true],
      dobleMano:[0,false],
      extras: [0,true],
      multi:1,
      
      level:1,
      atack:1,
    }
  },
  methods: {
    reset(){
      this.cabeza=[0,true]
      this.cuerpo=[0,true]
      this.pies=  [0,true]
      this.brazo1=[0,true]
      this.brazo2=[0,true]
      this.dobleMano=[0,false]
      this.extras=[0,true],
      this.level=1
      this.atack=1
      this.multi=1
      this.levelM()
    },
    sum(){
      this.level++
      this.levelM()
    },
    min(){
      this.level--
      this.levelM()
      
    },
    levelM(){
      console.log('Calculando...')
      let atack=0
      if(this.cabeza[1]){
        atack+=this.cabeza[0]
      }
      if(this.cuerpo[1]){
        atack+=this.cuerpo[0]
      }
      if(this.pies[1]){
        atack+=this.pies[0]
      }

      if(this.extras[1]){
        atack+=this.extras[0]
      }

      if(this.dobleMano[1]){
        atack+=this.dobleMano[0]
      }else{
        if(this.brazo1[1]){
          atack+=this.brazo1[0]
        }
        if(this.brazo2[1]){
          atack+=this.brazo2[0]
        }
      }

      
      this.atack=(atack+this.level)*this.multi;
      
    }
  },

}
</script>

